<template>
  <div class="sz-settlement-component del-arrow-up">
    <el-select
      v-model="inputValue"
      :clearable="clearable"
      :filterable="true"
      :disabled="disabled"
      :filter-method="remote"
      @change="changeItem"
      @clear="clear"
      @blur="blur"
      no-match-text=" "
      :placeholder="placeholder"
      :popperClass="popperClass"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.name + item.code"
        :value="item.id"
      />
    </el-select>
  </div>
</template>

<script>
/**
 * v-modelid
 * model
 * id
 * clearable
 * name *  * ^code^ :"^code^ " * ^name^ :^name^ " * ^all^  *:^all^ " * ^custom^ %:^custom^ " java.lang.String
 *parentId *  * idchild=true java.lang.Long
 *provId * id java.lang.Long
 *provIds * id java.util.List[]
 *cityId * id java.lang.Long
 *cityIds * id java.util.List[]
 *level *  1:2345child java.lang.Integer
 *child *  java.lang.Boolean
 *self *  java.lang.Boolean
 *recursion * parentId boolean
 *manageAreas * , java.util.List[]
 *enabled * (0/1) java.lang.Integer
 *inSiteIds * in id java.util.List[]
 *noSiteIds * not in id java.util.List[]
 *permission true java.lang.Boolean
 *id
 */
import { getSiteData } from "@/config/api";
import { truncate } from 'fs';
export default {
  props: {
    value: {
      type: [Number, String]
    },
    default: [Number, String],
    clearable: {
      type: Boolean,
      default: true
    },
    hasSubordinate: {
      type: Boolean,
      default: true
    },
    placeholder: {
      type: String,
      default: ""
    },
    popperClass: {
      type: String,
      default: ""//acconunt-name-class
    },
    // 
    param: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      inputValue: this.value,
      options: [],
      globalOptions: [],
      loading: false,
      disabled: false
    };
  },
  mounted() {
    this.getSettlement();
  },
  methods: {
    // 
    remote: _.debounce(function(key) {
      this.getSettlement(key);
    }, 1000),
    // 
    getSettlement(key) {
      if(!this.hasSubordinate){
        this.disabled = true;
        this.options = [{
          "id":this.z_param.siteInfo.id,
          "name":this.z_param.siteInfo.siteName,
          "code":this.z_param.siteInfo.siteCode
        }];
        this.$emit("input", this.z_param.siteInfo.id);
      }else{
        let options = {
          name: key,
          ...this.param
        };
      getSiteData(options).then(res => {
        if (res.data && res.data.length > 0) {
          this.options = res.data;
          if (this.globalOptions.length === 0) {
            this.globalOptions = res.data;
          }
        } else {
          // this.msg("");
          this.options = [];
        }
      }).catch((response) => {});
      }
    },
    // 
    changeItem(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.$emit("input", this.inputValue);
      this.$emit("model", this.chooseObj);
    },
    chooseOne(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.z_param.arrInfo = [];
      if (chooseObj != null) {
        if (chooseObj.type == 4) {
          this.z_param.arrInfo.push(chooseObj);
        }
      }
    },
    chooseId(key,keyId, callBack) {
      if(!this.hasSubordinate){
        callBack(self, this.inputValue);
        // this.disabled = true;
        // this.options = [{
        //   "id":this.z_param.siteInfo.id,
        //   "name":this.z_param.siteInfo.siteName,
        //   "code":this.z_param.siteInfo.siteCode
        // }];
        // this.$emit("input", this.z_param.siteInfo.id);
      }else{
        let options = {
          name: key,
          ...this.param
        };
      getSiteData(options).then(res => {
        if (res.data && res.data.length > 0) {
          this.options = res.data;
          if (this.globalOptions.length === 0) {
            this.globalOptions = res.data;
          }
        } else {
          // this.msg("");
          this.options = [];
        }
        this.$emit("input", keyId);
        callBack(self, this.inputValue);
      }).catch((response) => {});
      }
    },
    blur() {
      if (this.options.length === 0) {
        setTimeout(() => {
          this.options = this.globalOptions;
        }, 1000);
      }
    },
    clear() {
      this.inputValue = "";
      this.options = this.globalOptions;
      // this.getSettlement();
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: "warning",
        duration: 5000
      });
    }
  },
  watch: {
    value(value, oldValue) {
      this.inputValue = value;
      this.$emit("input", value);
    }
  }
};
</script>

